<template>
	<div>
		<el-upload
			class="avatar-uploader"
			v-loading="loading"
			ref="uploadRef"
			:action="uploadUrl"
			:show-file-list="false"
			:before-upload="beforeUpload"
			:on-error="uploadError"
			:on-success="uploadSuccess"
			:on-progress="uploadProcess"
		>
			<!-- :http-request="httpUpload" -->
			<div v-if="!fileFlag && fileType === 'image'">
				<img v-if="fileUrl" :src="fileUrl" class="avatar" />
				<i v-else class="el-icon-plus avatar-uploader-icon" />
			</div>
			<div v-if="!fileFlag && fileType === 'video'">
				<video v-if="fileUrl" :src="fileUrl" class="avatar-video" controls="controls">  </video>
				<i v-else class="el-icon-plus avatar-uploader-icon" />
			</div>
			<el-progress v-if="fileFlag" style="margin-top: 10px" :percentage="uploadPercent" :format="percentTip" />
		</el-upload>
	</div>
</template>

<script>
export default {
	name: 'EleBaseUpload',
	props: {
		uploadUrl: {
			// api
			type: String,
			required: true
		},
		fileType: {
			// 
			type: String,
			default: 'image'
		},
		hasFile: {
			// 
			type: String,
			default: ''
		}
	},
	data() {
		return {
			fileUrl: this.hasFile || '', // 
			loading: false, // 
			uploadPercent: 0, // 
			fileFlag: false // 
		}
	},
	methods: {
		// 
		beforeUpload(file) {
			const isImage = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg'
			const isVideo = file.type === 'video/mp4' || file.type === 'video/avi' || file.type === 'video/flv'
			const isLt10M = file.size / 1024 / 1024 < 10
			if (!isLt10M) {
				this.$message.error(' 10MB!')
				return false
			}
			if (!isImage && this.fileType === 'image') {
				this.$message.error('!')
				return false
			}
			if (!isVideo && this.fileType === 'video') {
				this.$message.error('!')
				return false
			}
		},
		// 
		httpUpload(file) {
			console.log(file)
			this.fileUrl = ''
			this.uploadPercent = 0
			const formData = new FormData()
			formData.append('file', file)
			// formData.append('appId', this.prefixApi)
			//	this.uploadError(file) 
			this.loading = true
			setTimeout(() => {
				this.fileUrl = 'https://img13.360buyimg.com/seckillcms/s140x140_jfs/t1/146322/40/21521/49848/6206310fEe5e7452d/efa5d320c8e3cffb.jpg.webp'
				this.loading = false
			}, 1000)
		},
		// 
		uploadSuccess(res) {
			this.fileFlag = false
			this.uploadPercent = 0
			if (res.code === 200) {
				this.fileUrl = res.data
			} else {
				this.$message.error('')
			}
		},
		// 
		uploadError(err, file) {
			console.log(err)
			const { uploadRef } = this.$refs
			const { uid } = file
			const idx = uploadRef.uploadFiles.findIndex((item) => item.uid === uid)
			uploadRef.uploadFiles.splice(idx, 1)
		},
		// 
		uploadProcess(event) {
			// event, file, fileList
			this.fileFlag = true
			this.uploadPercent = Math.floor(event.percent)
		},
		percentTip(percentage) {
			return percentage === 100 ? '' : `${percentage}%`
		}
	}
}
</script>

<style scoped lang="less">
::v-deep .avatar-uploader .el-upload {
	border: 1px dashed #d9d9d9;
	border-radius: 6px;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	&:hover {
		border-color: #409eff;
	}
	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 148px;
		height: 148px;
		line-height: 148px;
		text-align: center;
	}
}
.avatar {
	width: 148px;
	max-height: 148px;
	display: block;
}
.avatar-video {
	width: 200px;
	max-height: 200px;
	display: block;
}
</style>
